@import url(https://fonts.googleapis.com/css?family=Comfortaa);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Comfortaa', cursive;
}

.App-logo {
  pointer-events: none;
  height: 33vmin;
}

.App-logo-small {
     height: 15vmin;
    -webkit-align-self: flex-start;
            align-self: flex-start;
}

.App-logo-small img{

}

.App-header {
  background-color: #282c34;
  min-height: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 10vh;
}

.App-link {
  color: #61dafb;
}


.cities {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  max-width: 1000px;
}

.cities ruby {
  margin: 20px auto;
  letter-spacing: 9px;
  font-size: 25px;
   font-family: 'Comfortaa', cursive;
}

.cities ruby rt {
    letter-spacing: 9px;
  font-size: 20px;
   font-family: 'Comfortaa', cursive;
}

.cities h4 {
    font-size: 25px;
}

.btn {
  font-family: 'Comfortaa', cursive;
  border-radius: 10px;
  background-color: #282c34;
  font-size: 25px;
  margin: 40px auto 0;
  color: white;
}

.footer {
  min-height: 50%;
}

#enter {
    cursor: pointer;
    background-color: #282c34;
    border-radius: 50%;
    transition: background-color, background-position .4s ease;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    height: 70px;
    width: 70px;
    margin: 10px auto;
    background-image: linear-gradient(to right, #fff, #fff 50%, transparent 50%);
    background-size: 201% 100%;
    background-position: 100%;
    position: relative;
    text-decoration: none;
    color: white;
    font-family: 'Comfortaa', cursive;
    font-size: 20px;
    letter-spacing: 1px;
}

#enter::after {
    content: '';
    height: 3px;
    width: 0;
    background-color: #282c34;
    top: 60px;
    left: 0;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    position: absolute;
}

 #enter:hover {
    color: #282c34;
     background-position: 0;
  }
 #enter:hover::after {
       width: 100%;
     transition: all .7s ease;
 }

 .section-padding {
   padding: 25px;
 }

 @media screen and (max-width: 500px) {
     .cities {
         -webkit-flex-direction: column;
                 flex-direction: column;
     }
     .cities h4 {
         display: none;
     }
 }
.portfolio-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    max-height: 100%;
    transition: all .5s ease;
    opacity: 1;
}

.portfolio-container.hide {
    max-height: 0;
    overflow: hidden;
    transition: all .5s ease;
    opacity: 0;
}

.portfolio-box {
    padding: 20px;
    width: calc(50% - 40px);
    min-height: 20vh;
    background-color: #282c34;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    transition: all .5s ease;
}

.portfolio-box1 {
    background-color: #79458D;
}

.portfolio-box2 {
    background-color: #163F7A;
}

.portfolio-box3 {
    background-color: #014B97;
}

.portfolio-box4 {
    background-color: #59ADB8;
}

.portfolio-box img{
    max-width: 500px;
    height: auto;
    width: 100%;
}

.portfolio-box:hover {
    cursor: pointer;
     opacity: 0.8;
}


.box {
  display: block;
  width: 200px;
  height: 200px;
  background-color: gray;
  color: white;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
}
.box.green {
  background-color: green;
}

.Navbar {
    background-color: #282c34;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    color: white;
    padding: 10px;
    min-width: 500px;
}
