@import url('https://fonts.googleapis.com/css?family=Comfortaa');

.App {
  text-align: center;
  font-family: 'Comfortaa', cursive;
}

.App-logo {
  pointer-events: none;
  height: 33vmin;
}

.App-logo-small {
     height: 15vmin;
    align-self: flex-start;
}

.App-logo-small img{

}

.App-header {
  background-color: #282c34;
  min-height: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 10vh;
}

.App-link {
  color: #61dafb;
}


.cities {
  display: flex;
  margin: auto;
  max-width: 1000px;
}

.cities ruby {
  margin: 20px auto;
  letter-spacing: 9px;
  font-size: 25px;
   font-family: 'Comfortaa', cursive;
}

.cities ruby rt {
    letter-spacing: 9px;
  font-size: 20px;
   font-family: 'Comfortaa', cursive;
}

.cities h4 {
    font-size: 25px;
}

.btn {
  font-family: 'Comfortaa', cursive;
  border-radius: 10px;
  background-color: #282c34;
  font-size: 25px;
  margin: 40px auto 0;
  color: white;
}

.footer {
  min-height: 50%;
}

#enter {
    cursor: pointer;
    background-color: #282c34;
    border-radius: 50%;
    transition: background-color, background-position .4s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 70px;
    width: 70px;
    margin: 10px auto;
    background-image: linear-gradient(to right, #fff, #fff 50%, transparent 50%);
    background-size: 201% 100%;
    background-position: 100%;
    position: relative;
    text-decoration: none;
    color: white;
    font-family: 'Comfortaa', cursive;
    font-size: 20px;
    letter-spacing: 1px;
}

#enter::after {
    content: '';
    height: 3px;
    width: 0;
    background-color: #282c34;
    top: 60px;
    left: 0;
    transform: translateX(0%);
    position: absolute;
}

 #enter:hover {
    color: #282c34;
     background-position: 0;
  }
 #enter:hover::after {
       width: 100%;
     transition: all .7s ease;
 }

 .section-padding {
   padding: 25px;
 }

 @media screen and (max-width: 500px) {
     .cities {
         flex-direction: column;
     }
     .cities h4 {
         display: none;
     }
 }